@media (min-width: 768px) {
  .md\:mx-8 {
    margin-left: 2rem;
    margin-right: 2rem
  }

  .md\:mt-0 {
    margin-top: 0px
  }

  .md\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr))
  }
}

@media (min-width: 1024px) {
  .lg\:bottom-10 {
    bottom: 2.5rem
  }

  .lg\:col-span-5 {
    grid-column: span 5 / span 5
  }

  .lg\:col-span-7 {
    grid-column: span 7 / span 7
  }

  .lg\:mb-0 {
    margin-bottom: 0px
  }

  .lg\:flex {
    display: flex
  }

  .lg\:px-14 {
    padding-left: 3.5rem;
    padding-right: 3.5rem
  }
}

